import { Pipe, PipeTransform } from '@angular/core';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';

@Pipe({
  name: 'bauchondanhhieu'
})
export class BauchondanhhieuPipe implements PipeTransform {
  constructor(private sanitizer: DomSanitizer) { }
  transform(value: any, args?: any): SafeHtml {
    if (value === 0) {
      return this.sanitizer.bypassSecurityTrustHtml('<span class="badge badge-brand">Cha bu chn</span>');
    }
    if (value === 1) {
      return this.sanitizer.bypassSecurityTrustHtml('<span class="badge badge-primary">ang bu chn</span>');
    }
    if (value === 2) {
      return this.sanitizer.bypassSecurityTrustHtml('<span class="badge badge-success"> kt thc</span>');
    }
    return null;
  }

}
