import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'trangThaiHoiDong'
})
export class TrangThaiHoiDongPipe implements PipeTransform {
  transform(value: any, args?: any): any {
    switch (value) {
      case 0:
        return 'Cha m';
      case 1:
        return ' ng';
      case 2:
        return 'ang m';
      default:
      return 'Khng xc nh';
    }
  }
}
